      latest_kline:{},
      kline_type: '1',

      kline_data: [],
      echartDom:{},
      klineData:[],
      klineOption:{},

    }
  },

  methods: {
    switchEntrustForm(tab, event) {
      console.log(tab, event);
    },
    switchOrderListCard(tab, event) {
      if (this.entrustCard === 'history') {
        this.$myHttp.post("/order/v1/get_order_list", {'symbol_name': 'BTC_USDT', 'status_list': [3, 4]}).then(resp => {
          if (resp.status !== 200) {
            this.$message.error('')
            return
          }
          if (resp.data.code !== 0) {
            this.$message.error(resp.data.msg)
            localStorage.removeItem('gexToken')
            return
          }
          resp.data.data.order_list.forEach(el => {
            el.side = el.side ===1 ? '' :''
            el.symbol = el.symbol_name
            el.date = castTimeStamp(el.created_at)
            el.order_type= el.order_type ===1?'':''
            el.status= el.status ===3?'':''
          })
          this.historyOrderList = resp.data.data.order_list
        })
      }else{
         this.getCurrentUserOrder()
      }
    },
    validateToken() {
      return localStorage.getItem('gexToken')
    },
    submitLoginForm() {
      this.$myHttp.post("/account/v1/login", this.loginForm).then(loginResp => {
        if (loginResp.status !== 200) {
          this.$message.error('')
        }
        if (loginResp.data.code !== 0) {
          this.$message.error(loginResp.data.msg)
        }
        this.$message.success('!')
        localStorage.setItem('gexToken', loginResp.data.data.token)
        this.showLoginDialogForm = false
      }).catch(el=>{
        console.log(el)
      })
    },
    getUserAsset() {
      this.$myHttp.post("/account/v1/get_user_asset_list").then(resp => {
        if (resp.status !== 200) {
          this.$message.error('')
          return
        }
        if (resp.data.code !== 0) {
          this.$message.error(resp.data.msg)
          return
        }
        this.asset_data = resp.data.data.asset_list
      })
    },
    //
    getDepthList() {
      this.$myHttp.post("/quotes/v1/get_depth_list",
        {'symbol': 'BTC_USDT', 'level': 200}).then(resp => {
        if (resp.status !== 200) {
          this.$message.error('')
          return
        }
        if (resp.data.code !== 0) {
          this.$message.error(resp.data.msg)
          return
        }
        this.askList = resp.data.data.asks
        this.bidList = resp.data.data.bids
        this.currentVersion = resp.data.version
        this.askList.forEach((data) => {
          let q = new Decimal(data.qty)
          if (q.gt(this.askMaxQty)) {
            this.askMaxQty = q
          }
        })
        this.bidList.forEach((data) => {
          let q = new Decimal(data.qty)
          if (q.gt(this.bidMaxQty)) {
            this.bidMaxQty = q
          }
        })
      })
    },
    //
    getTickList() {
      this.$myHttp.post("/quotes/v1/get_tick_list",
        {'symbol': 'BTC_USDT', 'limit': 20}).then(resp => {
        if (resp.status !== 200) {
          this.$message.error('')
          return
        }
        if (resp.data.code !== 0) {
          this.$message.error(resp.data.msg)
          return
        }

        resp.data.data.tick_list.forEach(data => {

          data.filled_time = castTimeStamp2Time(data.timestamp)

        })
        this.filledOrderList = resp.data.data.tick_list
      })
    },
    filledOrderListClassName({row, rowIndex}) {
      if (row.taker_is_buyer) {
        return 'filledOrderBuy'
      }
      return 'filledOrderSell'
    },
    //
    buyLoOrderOrLogin(formName) {
      //
      if (!this.isLogin) {
        this.showLoginDialogForm = true
        return
      }
      //
      this.$myHttp.post("/order/v1/create_order", this.loBuyForm).then(resp => {
        if (resp.status !== 200) {
          this.$message.error('')
          return
        }
        if (resp.data.code !== 0) {
          this.$message.error(resp.data.msg)
          return
        }
        this.$message.success('')
      })
      this.$refs[formName].resetFields()

    },
    sellLoOrderOrLogin(formName) {
      //
      if (!this.isLogin) {
        this.showLoginDialogForm = true
        return
      }
      //
      this.$myHttp.post("/order/v1/create_order", this.loSellForm).then(resp => {
        if (resp.status !== 200) {
          this.$message.error('')
          return
        }
        if (resp.data.code !== 0) {
          this.$message.error(resp.data.msg)
          return
        }
        this.$message.success('')
      })
      this.$refs[formName].resetFields();

    },
    buyMoOrderOrLogin(formName) {
      //
      if (!this.isLogin) {
        this.showLoginDialogForm = true
        return
      }
      //
      this.$myHttp.post("/order/v1/create_order", this.moBuyForm).then(resp => {
        if (resp.status !== 200) {
          this.$message.error('')
          return
        }
        if (resp.data.code !== 0) {
          this.$message.error(resp.data.msg)
          return
        }
        this.$message.success('')
      })
      this.$refs[formName].resetFields();

    },
    sellMoOrderOrLogin(formName) {
      //
      if (!this.isLogin) {
        this.showLoginDialogForm = true
        return
      }
      //
      this.$myHttp.post("/order/v1/create_order", this.moSellForm).then(resp => {
        if (resp.status !== 200) {
          this.$message.error('')
          return
        }
        if (resp.data.code !== 0) {
          this.$message.error(resp.data.msg)
          return
        }
        this.$message.success('')
      })
      this.$refs[formName].resetFields();

    },
    //
    getCurrentUserOrder() {
      this.$myHttp.post("/order/v1/get_order_list", {'symbol_name': 'BTC_USDT', 'status_list': [1, 2]}).then(resp => {
        if (resp.status !== 200) {
          this.$message.error('')
          return
        }
        if (resp.data.code !== 0) {
          this.$message.error(resp.data.msg)
          localStorage.removeItem('gexToken')
          return
        }
        resp.data.data.order_list.forEach(el => {
          el.side = el.side ===1 ? '' :''
          el.symbol = el.symbol_name
          el.date = castTimeStamp(el.created_at)
          el.filledUnfilled = el.filled_qty + '/' + el.qty
        })
        this.currentOrderList = resp.data.data.order_list
      })
    },
    reload(){
      this.getKlineData().then(()=>{
        let klineType = ''
        switch (this.kline_type){
          case '1':

            klineType ='Min1'
            break
          case '5':

            klineType ='Min5'
            break
          case '10':

            klineType ='Min10'
            break
          case '15':

            klineType ='Min15'
            break
          case '30':

            klineType ='Min30'
            break
          case '1':

            klineType ='Hour1'
            break
          case '4':

            klineType ='Hour4'
            break
          case '1':

            klineType ='Day1'
            break
          case '1':

            klineType ='Week1'
            break
          case '1':
            klineType ='Month1'

            break
        }
        let wdData =   {'code': 1, 'topic': 'kline@BTC_USDT@'+klineType}
        //this.webSocketConn.send(JSON.stringify(wdData))
        this.myEcharts()
      })
    },
    splitData(rawData) {
      let categoryData = [];
      let values = [];
      let volumes = [];
      for (let i = 0; i < rawData.length; i++) {
        categoryData.push(rawData[i].splice(0, 1)[0]);
        values.push(rawData[i]);
        volumes.push([i, rawData[i][4], rawData[i][0] > rawData[i][1] ? 1 : -1]);
      }
      return {
        categoryData: categoryData,
        values: values,
        volumes: volumes
      };
    },
    myEcharts() {
      const chartDom = document.getElementById('kline');
      this.echartDom = echarts.init(chartDom);
      let data = this.splitData(this.kline_data)
      console.log(data)
      this.klineOption = {
          animation: false,
          legend: {
            bottom: 10,
            left: 'center',
            data: ['Dow-Jones index']
          },
          tooltip: {
