            },
            borderWidth: 1,
            borderColor: '#ccc',
            padding: 10,
            textStyle: {
              color: '#000'
            },
            position: function (pos, params, el, elRect, size) {
              const obj = {
                top: 10
              };
              obj[['left', 'right'][+(pos[0] < size.viewSize[0] / 2)]] = 30;
              return obj;
            }
            // extraCssText: 'width: 170px'
          },
          axisPointer: {
            link: [
              {
                xAxisIndex: 'all'
              }
            ],
            label: {
              backgroundColor: '#777'
            }
          },
          toolbox: {
            feature: {
              dataZoom: {
                yAxisIndex: false
              },
              brush: {
                type: ['lineX', 'clear']
              }
            }
          },
          brush: {
            xAxisIndex: 'all',
            brushLink: 'all',
            outOfBrush: {
              colorAlpha: 0.1
            }
          },
          visualMap: {
            show: false,
            seriesIndex: 5,
            dimension: 2,
            pieces: [
              {
                value: 1,
                color: '#ec0000'
              },
              {
                value: -1,
                color: '#00da3c'
              }
            ]
          },
          grid: [
            {
              left: '10%',
              right: '8%',
              height: '50%'
            },
            {
              left: '10%',
              right: '8%',
              top: '63%',
              height: '16%'
            }
          ],
          xAxis: [
            {
              type: 'category',
              data: data.categoryData,
              boundaryGap: false,
              axisLine: { onZero: false },
              splitLine: { show: false },
              min: 'dataMin',
              max: 'dataMax',
              axisPointer: {
                z: 100
              }
            },
            {
              type: 'category',
              gridIndex: 1,
              data: data.categoryData,
              boundaryGap: false,
              axisLine: { onZero: false },
              axisTick: { show: false },
              splitLine: { show: false },
              axisLabel: { show: false },
              min: 'dataMin',
              max: 'dataMax'
            }
          ],
          yAxis: [
            {
              scale: true,
              splitArea: {
                show: true
              }
            },
            {
              scale: true,
              gridIndex: 1,
              splitNumber: 2,
              axisLabel: { show: false },
              axisLine: { show: false },
              axisTick: { show: false },
              splitLine: { show: false }
            }
          ],
          dataZoom: [
            {
              type: 'inside',
              xAxisIndex: [0, 1],
              start: 90,
              end: 100
            },
            {
              show: true,
              xAxisIndex: [0, 1],
              type: 'slider',
              top: '85%',
              start: 98,
              end: 100
            }
          ],
          series: [
            {
              name: 'Dow-Jones index',
              type: 'candlestick',
              data: data.values,
              itemStyle: {
                color: '#00da3c',
                color0: '#ec0000',
                borderColor: undefined,
                borderColor0: undefined
              }
            },
            {
              name: 'Volume',
              type: 'bar',
              xAxisIndex: 1,
              yAxisIndex: 1,
              data: data.volumes
            }
          ]
        }
      this.echartDom.setOption(this.klineOption);

    },
    async getKlineData() {
      let st = 0
      let kline_type=0
      switch (this.kline_type){
        case '1':
          st=1
          kline_type ='Min1'
          break
        case '5':
          st=2
          kline_type ='Min5'
          break
        case '10':
          st=3
          kline_type ='Min10'
          break
        case '15':
          st=4
          kline_type ='Min15'
          break
        case '30':
          st=5
          kline_type ='Min30'
          break
        case '1':
          st=6
          kline_type ='Hour1'
          break
        case '4':
          st=7
          kline_type ='Hour4'
          break
        case '1':
          st=8
          kline_type ='Day1'
          break
        case '1':
          st=9
          kline_type ='Week1'
          break
        case '1':
          kline_type ='Month1'
          st=10
          break
      }
      this.kline_data=[]
      await this.$myHttp.post("/quotes/v1/get_kline_list",
        {
          "start_time": 0,
          "end_time": 1479374820143,
          "kline_type": st,
          "symbol": "BTC_USDT"
        }).then(resp => {
        if (resp.status !== 200) {
          this.$message.error('')
          return
        }
        if (resp.data.code !== 0) {
          this.$message.error(resp.data.msg)
          return
        }
        resp.data.data.kine_list.forEach(
          d => {
            let item = [castTimeStamp(d.start_time),d.open,d.close,d.low,d.high,d.volume]
            this.kline_data.push(item)
          })
      })

    },
    getTicker() {
      this.$myHttp.post("/quotes/v1/get_ticker_list", {'symbol': 'BTC_USDT'}).then(resp => {
        if (resp.status !== 200) {
          this.$message.error('')
          return
        }
        if (resp.data.code !== 0) {
          this.$message.error(resp.data.msg)
          return
        }
        this.last_price = resp.data.data.ticker_list[0].last_price
        this.tickerList = resp.data.data.ticker_list

      })
    },
    cancelOrder(index, row) {
      this.$myHttp.post("/order/v1/cancel_order", {'symbol_name': 'BTC_USDT', 'id': row.id}).then(resp => {
        if (resp.status !== 200) {
          this.$message.error('')
          return
        }
        if (resp.data.code !== 0) {
          this.$message.error(resp.data.msg)
        }
      })
    },
    async initWebsocketConn() {
      // onopen promise resolve
      await this.connect()
      const subs = [
        {'code': 1, 'topic': 'kline@BTC_USDT@Min1'},
        {'code': 1, 'topic': 'tick@BTC_USDT'},
        {'code': 1, 'topic': 'depth@BTC_USDT'},
        {'code': 1, 'topic': 'ticker@BTC_USDT'},
      ]
      if (this.isLogin) {
        let loginReq = {'code': 3, 'data': localStorage.getItem('gexToken')}
        this.webSocketConn.send(JSON.stringify(loginReq))
        let orderReq = {'code': 1, 'topic': 'order@BTC_USDT'}
        subs.push(orderReq)
      }
      const tickTopic = 'tick'
      const tickerTopic = 'ticker'
      const depthTopic = 'depth'
      const klineTopic = 'kline'
      const orderTopic = 'order'
      subs.forEach((el) => {
        this.webSocketConn.send(JSON.stringify(el))
      })
      this.webSocketConn.onmessage = (data) => {
        let resp = JSON.parse(data.data)

        if (resp.code >= 20000) {
          return
        }
        let prefix = resp.t.split("@")[0]
        switch (prefix) {
          case tickerTopic:
            console.log(resp)
            this.tickerList.forEach(el=>{
              if (el.symbol === resp.t.split("@")[1]) {
                el.last_price = resp.p.lp
                el.price_range = resp.p.r
                this.last_price= resp.p.lp
              }
            })
            break
          case depthTopic:
            let newAsks = []
            const decimalZero = new Decimal(0)
            resp.p.a.forEach((d) => {
              let existed = false
              let pos = {}
              //
              this.askList.forEach((el) => {
                if (d[0] === el.price) {
                  el.qty = d[1]
                  el.amount = d[2]
                  existed = true
                }
              })
              if (new Decimal(d[1]).eq(decimalZero)) {
                return
              }
              pos = {'price': d[0], 'qty': d[1], 'amount': d[2]}
              if (!existed) {
                newAsks.push(pos)
              }
            })
            this.askList.forEach(d => {
              if (new Decimal(d.qty).eq(decimalZero)) {
                return
              }
              newAsks.push(d)
            })
            //
            this.askMaxQty = new Decimal(0)
            newAsks.forEach(el => {
              let qty = new Decimal(el.qty)
              if (qty.gt(this.askMaxQty)) {
                this.askMaxQty = qty
              }

            })
            this.askList = newAsks.sort((v1, v2) => {
              const v1Price = new Decimal(v1.price)
              const v2Price = new Decimal(v2.price)
              return -v1Price.comparedTo(v2Price)
            })
            let newBids = []
            resp.p.b.forEach((d) => {
              let existed = false
              let pos = {}
              //
              this.bidList.forEach((el) => {
                //
                if (d[0] === el.price) {
                  el.qty = d[1]
                  el.amount = d[2]
                  existed = true
                }
              })
              //
              if (new Decimal(d[1]).eq(decimalZero)) {
                return
              }
              pos = {'price': d[0], 'qty': d[1], 'amount': d[2]}
              //
              if (!existed) {
                newBids.push(pos)
              }
            })
            this.bidList.forEach(d => {
              if (new Decimal(d.qty).eq(decimalZero)) {
                return
              }
              newBids.push(d)
            })
            //
            this.bidMaxQty = new Decimal(0)
            newBids.forEach(el => {
              let qty = new Decimal(el.qty)
              if (qty.gt(this.bidMaxQty)) {
                this.bidMaxQty = qty
              }

            })

            this.bidList = newBids.sort((v1, v2) => {
              const v1Price = new Decimal(v1.price)
              const v2Price = new Decimal(v2.price)
              return -v1Price.comparedTo(v2Price)
            })
            //  console.log(this.bidList)
            break
          case tickTopic:
            let obj = {
              'price': resp.p.p,
              'qty': resp.p.q,
              'filled_time': castTimeStamp2Time(resp.p.ts),
              'taker_is_buyer': resp.p.tib
            }
            this.filledOrderList.unshift(obj)
            this.filledOrderList.pop()
            break
          case klineTopic:
            /*{"t":"kline@BTC_USDT@Min1","p":{"kt":1,"o":"3","h":"3","l":"3","c":"3","v":"0","t":"0","st":1694953020,"et":1694953080,"r":"0.000","s":"BTC_USDT"}}*/
            let klineCycle =''
            switch (this.kline_type){
              case '1':
                klineCycle ='Min1'
                break
              case '5':
                klineCycle ='Min5'
                break
              case '10':
                klineCycle ='Min10'
                break
              case '15':
                klineCycle ='Min15'
                break
              case '30':
                klineCycle ='Min30'
                break
              case '1':
                klineCycle ='Hour1'
                break
              case '4':
                klineCycle ='Hour4'
                break
              case '1':
                klineCycle ='Day1'
                break
              case '1':
                klineCycle ='Week1'
                break
              case '1':
                klineCycle ='Month1'
                break
            }
            if (resp.t.split("@")[2] !== klineCycle) {
             return
            }
            let l = this.klineOption.series[0].data.length
            let item = [resp.p.o,resp.p.c,resp.p.l,resp.p.h,resp.p.v]

            if (this.klineOption.xAxis[0].data[l - 1] === castTimeStamp(resp.p.st)) {
              let lastCategory = [l-1,resp.p.v,-1]
              this.klineOption.series[0].data[l-1]=item
              this.klineOption.series[1].data[l-1]=lastCategory
            }else{
              let lastCategory = [l,resp.p.v,-1]
              this.klineOption.series[0].data.push(item)
              this.klineOption.xAxis[0].data.push(castTimeStamp(resp.p.st))
              this.klineOption.series[1].data.push(lastCategory)
            }
            console.log(this.klineOption.series)
            this.echartDom.setOption(this.klineOption)
            break
          case orderTopic:
            switch (resp.p.s){
              case 1:
                let order = {
                  'id': resp.p.id,
                  'order_id': resp.p.oi,
                  'user_id': resp.p.u,
                  'symbol': resp.p.sn,
                  'price': resp.p.p,
                  'qty': resp.p.q,
                  'amount': resp.p.a,
                  'side': resp.p.si ===1 ? '':'',
                  'status': resp.p.s,
                  'order_type': resp.p.ot,
                  'filled_qty': resp.p.fq,
                  'filled_amount': resp.p.fa,
                  'filled_avg_price': resp.p.fap,
                  'date': castTimeStamp(resp.p.ca),
                  'filledUnfilled':resp.p.fq+'/'+resp.p.q,
                }
                this.currentOrderList.unshift(order)
                break
              case 4:
                this.currentOrderList = this.currentOrderList.filter(el=>{
                  return el.id !== resp.p.id;
                })
                break
              case 2:
                this.currentOrderList.forEach(el=>{
                  el.filledUnfilled = resp.p.fq+'/'+el.qty
                })
                break
              case 3:
                this.currentOrderList = this.currentOrderList.filter(el=>{
                  return el.id !== resp.p.id;
                })
            }


            if (resp.p.s === 2){


            }
            break
        }

      }
    },
    filledOrderCell({rowIndex, columnIndex, row, column}) {
      if (row.taker_is_buyer && columnIndex === 0) {
        return "color :  #31BD65";
      }
      if (!row.taker_is_buyer && columnIndex === 0) {
        return "color : #EB4F70";
      }

    },
    tickerListStyle({rowIndex, columnIndex, row, column}){

      if (columnIndex === 2){

        if (row.price_range < 0) {
          return "color : #EB4F70";
        }

        if (row.price_range >= 0) {
          return "color : #31BD65";
        }
      }

    },
    askListCell({rowIndex, columnIndex, row, column}) {
      if (columnIndex === 0) {
        return `color: #EB4F70;text-align:left;border: none;padding: 0;font-family:"Hiragino Sans GB";margin: 5px 0;`
      }
      return `text-align:right;border: none;padding: 5px 0;font-size: 13px;font-family:"Hiragino Sans GB";margin: 5px 0;`
    },
    askListRow({row, rowIndex}) {
      let qty = new Decimal(row.qty)
      let sc = qty.div(this.askMaxQty).mul(new Decimal(100)).ceil().toFixed(0, Decimal.ROUND_CEIL)
      return {'background-image': `linear-gradient(to left,#F1D9DE ` + sc + `%, #FFFFFF ` + sc + `%  )`}
    },

    bidListCell({rowIndex, columnIndex, row, column}) {
      if (columnIndex === 0) {
        return `color: #31BD65;text-align:left;border: none;padding: 0;font-family:"Hiragino Sans GB";margin: 5px 0;`
      }
      return `text-align:right;border: none;padding: 5px 0;font-size: 13px;font-family:"Hiragino Sans GB";margin: 5px 0;`
    },
    bidListRow({row, rowIndex}) {
      console.log(this.bidMaxQty.toString())
      let qty = new Decimal(row.qty)
      let sc = qty.div(this.bidMaxQty).mul(new Decimal(100)).ceil().toFixed(0, Decimal.ROUND_CEIL)
      return {'background-image': `linear-gradient(to left,#F1D9DE ` + sc + `%, #FFFFFF ` + sc + `%  )`}
    },

    async connect() {
      await new Promise((resolve, reject) => {
        this.webSocketConn = new WebSocket("ws://api.gex.com/ws")
        this.webSocketConn.onopen = () => {
          console.log("connected")
          resolve()
        }
        this.webSocketConn.onerror = (error) => {
          reject(error)
        }
      })

    },

  },


  mounted() {
    this.isLogin = this.validateToken()
    if (this.isLogin) {
      this.loBuyButtonName = ''
      this.loSellButtonName = ''
      this.getUserAsset()
      this.getCurrentUserOrder()
    }
    this.getDepthList()
    this.getTickList()
    this.getTicker()
    this.initWebsocketConn()
    this.getKlineData().then(()=>{
      this.myEcharts()
    })
  },

}
</script>

<style scoped>
html, body {
  width: 100%;
  height: 100%;
}

.filledOrderSell {
  color: #EB4F70;
  font-size: 10px;
  height: 10px;
}

.filledOrderBuy {
  line-height: 5px;
  color: #31BD65;
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "", Arial, sans-serif;
  font-size: 10px;

}

.filledOrder >>> .el-table__row > td {
  border: none;
  padding: 5px;
}

.el-table::before {
  height: 0;
}

.height100 {
  width: 100%;
  padding: 0;
  margin: 0;
}


.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}

.clearfix:after {
  clear: both
}
</style>



